body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.sidebar{
    flex: 0.6 1;
    height: 650px;
    background-color: #15092d;
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
}

.sidebarWrapper{
    padding: 20px;
    color: white;
}

.sidebarMenu{
    margin-bottom: 10px;
}

.sidebarList{
    font-family: Calibri;
    color: white;
    font-size: 14px;
    margin-top: 0px;
    padding: 5px;
    list-style: none;
}


.sidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

/* .sidebarListItem.active,
.sidebarListItem:hover{
    background-color: red;
} */

.siderbarIcon{
    margin-right: 5px;
    font-size: 20px !Important;
    /* flex-direction: row; */
    
}
.SpeedIcon{
    visibility: hidden;
}


.topbar{
    width:100%;
    height: 50px;
    background-color: #15092d;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index:999;
}

.topbarWrapper{
    height: 100%;
    padding: 13px 20px;
    display: flex;
    color:white;
    /* justify-content: space-between; */
    text-transform: uppercase;
}

.logo{
    font-weight: bold;
    font-size: 14px;
    color: white;
    cursor: pointer;
    margin: 0 0 20px;
    height: 20px;
}

.topLeft{
    align-items: left;
}

.topbarIconContainer{
    position: relative;
}

.topRight{
    display: flex;
    align-items: right;
   
}

.topbarIconContainer{
    position: relative;
    cursor: pointer;
    margin: 10px;
    margin: right;
    color: #555;
}

.icon{
    align: right;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 70%;
    align: left;
}
.container{
  display: flex;
}

.IconLabelButtons{
  display: flex;
}
.home{
    flex: 3 1;
}

.IconLabelButtons{
    display: flex;
}

.topic{
    margin-top: 6px;
    border-radius: 1;
    font-size: 22px;
    font-family: Calibri;
    font-weight: bolder;
    padding: 10px;
    
}


.tabsd{
    width: 100%;
    font-size: 14px;
    text-transform: lowercase;

}


.Widgets{
    display: flex;
    margin: 12px;
    padding: 5px;
}

.btnnx{
    display: flex;
    height: 70px;
}

.buttond{
